import axios from "axios";
import {GET_GAME_URL, SEARCH_GAMES_URL, USER_INFO_URL, SEARCH_MOVIES_URL, GET_MOVIE_URL, SEARCH_USERS_URL} from "../settings";

let axiosConfig = {
    headers: {
        'Content-Type': 'application/json;charset=UTF-8'
    }
};

axios.defaults.headers.common = {
    "Content-Type": "application/json"
  }

/**
 *   ,    
 * @param {string} token  
 * @param {string} id ID   
 * @returns {object}   
 */
export async function getGame(token, id) {
    let data;
    try{
        if (token){
            var AuthStr = 'Bearer ' + token;
            const res = await axios.get(GET_GAME_URL + id + "/", { 'headers': { 'Authorization': AuthStr } });
            data = res.data;
        }else{
            const res = await axios.get(GET_GAME_URL + id + "/", axiosConfig);
            data = res.data;
        }
        return data;
    }catch(e){
        console.log("AXIOS ERROR: ", e);
        return null;
    }
}


